<template>
  <div>
    <el-tabs
      v-model="activename"
      @tab-click="handleClick"
      tab-position="left"
    >
      <el-tab-pane
        name="status"
        label="查看状态"
      >
        <p>标准要求：</p>
        <p>专业必须有明确、公开的毕业要求，毕业要求应能支撑培养目标的达成。专业应通过评价证明毕业要求的达成。</p>
        <p>专业制定的毕业要求应完全覆盖中国工程教育认证通用标准中所列的12项基本要求。</p>
        <el-table
          :data="statusTabledata"
          border
          style="width: 100%"
        >
          <el-table-column
            prop="name"
            width="250"
            label="材料名称"
          >
            <div slot-scope="scope">
              <router-link to="">
                {{ scope.row.name }}
              </router-link>
            </div>
          </el-table-column>

          <el-table-column
            width="180"
            prop="type"
            label="类型"
          />
          <el-table-column
            width="180"
            prop="status"
            label="状态"
          />
          <el-table-column
            prop="tips"
            label="备注"
          />
        </el-table>
      </el-tab-pane>

      <el-tab-pane
        name="association"
        label="协会毕业要求"
      >
        <div style="padding: 0 20px;">
          <h3>协会毕业要求</h3>
          <p>
            描述：
            专业必须有明确、公开的毕业要求，毕业要求应能支撑培养目标的达成。专业应通过评价证明毕业要求的达成。专业制定的毕业要求应完全覆盖以下内容。
          </p>
          <el-collapse @change="handleChange">
            <el-collapse-item
              class="card1"
              v-for="item in agrList"
              :key="item.name"
            >
              <template slot="title">
                <i class="iconfont icon-jiahao" /> {{ item.name }}
              </template>
              <div class="mtb20">
                描述：{{ item.desc }}
              </div>
              <div>与专业毕业要求建立对应关系</div>
              <el-table
                :data="item.table"
                border
                style="width: 100%"
              >
                <el-table-column
                  prop="name"
                  width="250"
                  label="专业毕业要求"
                />
                <el-table-column
                  prop="desc"
                  label="描述"
                />
              </el-table>
            </el-collapse-item>
          </el-collapse>
        </div>
      </el-tab-pane>

      <el-tab-pane
        name="speciality"
        label="专业毕业要求"
      >
        <div style="padding: 0 20px;">
          <p>描述： 暂无描述信息</p>
          <el-collapse
            @change="handleChange"
            v-for="(item, i) in specialityList"
            :key="i"
          >
            <el-collapse-item class="card1">
              <template slot="title">
                <i class="iconfont icon-jiahao" />{{ item.tmgr_tile }}
              </template>
              <div class="mtb20">
                描述：{{ item.tmgr_describe }}
              </div>

              <el-collapse
                @change="handleChange"
                v-for="(item1, y) in item.partsList"
                :key="y"
              >
                <el-collapse-item class="card1">
                  <template slot="title">
                    <i class="iconfont icon-jiahao" />{{ i + 1 + '.' + item1.num }}
                  </template>
                  <div class="mtb20">
                    描述：{{ item1.descript }}
                  </div>
                  <el-table
                    :data="item1.pointsList"
                    border
                    style="width: 100%"
                  >
                    <el-table-column
                      prop="table_course_tc_id.tc_name"
                      label="课程"
                    />
                    <el-table-column
                      prop="tip_weight_level"
                      label="权重级别"
                    />
                    <el-table-column
                      prop="tip_weight"
                      label="权重(%)"
                    />
                  </el-table>
                </el-collapse-item>
              </el-collapse>

              <!-- <div>与协会毕业要求建立对应关系</div> -->
              <el-table
                :data="item.major_index_point"
                border
                style="width: 100%"
              >
                <el-table-column
                  prop="tagr_title"
                  width="250"
                  label="协会毕业要求"
                />
                <el-table-column
                  prop="tagr_describe"
                  label="描述"
                />
              </el-table>
            </el-collapse-item>
          </el-collapse>
        </div>
      </el-tab-pane>
      <!-- 考核管理 -->
      <el-tab-pane
        nmae="assessment"
        label="考核管理"
        v-if="role !== 3"
      >
        <el-tabs
          v-model="activeName1"
          type="card"
          @tab-click="handleClick"
        >
          <el-tab-pane
            label="试卷管理"
            name="first"
          >
            <el-collapse @change="handleChange">
              <el-collapse-item
                class="card1"
                v-for="item in testList"
                :key="item.course.tc_id"
              >
                <template slot="title">
                  <i class="iconfont icon-jiahao" /> {{ item.course.tc_name }}
                </template>
                <div class="addtestbtn">
                  <el-button
                    type="primary"
                    @click="addtest(item)"
                  >
                    创建试卷
                  </el-button>
                </div>
                <el-table
                  :data="item.testList"
                  border
                  style="width: 100%"
                >
                  <el-table-column
                    prop="tt_id"
                    label="试卷ID"
                  />
                  <el-table-column
                    prop="tt_name"
                    label="试卷名称"
                  />
                  <el-table-column
                    prop="tt_reach"
                    label="达标度"
                  />
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button
                        type="warning"
                        size="mini"
                        icon="el-icon-edit"
                        @click.stop="edittest(scope.row)"
                      >
                        编辑
                      </el-button>
                      <el-button
                        type="danger"
                        size="mini"
                        icon="el-icon-delete"
                        @click.stop="deltest(scope.row)"
                      >
                        删除
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-collapse-item>
            </el-collapse>
          </el-tab-pane>
          <el-tab-pane
            label="成绩管理"
            name="second"
          >
            <el-collapse @change="handleChange">
              <el-collapse-item
                class="card1"
                v-for="item in courselist"
                :key="item.course.tc_id"
              >
                <template slot="title">
                  <i class="iconfont icon-jiahao" /> {{ item.course.tc_name }}
                </template>
                <el-upload
                  class="upload-demo"
                  name="excelTermFile"
                  :on-success="success"
                  action="http://localhost:10088/uploadTermFile"
                  :data="u_id"
                >
                  <el-button
                    type="success"
                    size="mini"
                    icon="el-icon-upload2"
                  >
                    上传成绩表
                  </el-button>
                </el-upload>
                <el-table
                  :data="item.stu_testList"
                  border
                  style="width: 100%"
                >
                  <el-table-column
                    prop="tst_test_name"
                    label="考核名称"
                  />
                  <el-table-column
                    prop="tst_term"
                    label="学期"
                  />
                  <el-table-column
                    prop="tst_test_type"
                    label="考核类型"
                  />
                  <el-table-column
                    prop="tst_test_second_test"
                    label="补考"
                  />
                  <el-table-column
                    prop="tst_teacher.u_name"
                    label="上传教师"
                  />
                  <el-table-column
                    prop="tst_upload"
                    label="上传时间"
                  />
                </el-table>
              </el-collapse-item>
            </el-collapse>
          </el-tab-pane>
        </el-tabs>
      </el-tab-pane>
      <el-tab-pane
        name="analysis"
        label="达成度分析"
        v-if="role !== 3"
      >
        <el-table
          :data="JudgmentTestlist"
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="tt_id"
            label="试卷ID"
          />
          <el-table-column
            prop="tt_name"
            label="试卷名称"
          />
          <el-table-column
            prop="tt_reach"
            label="达成度"
          />
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                type="warning"
                size="mini"
                icon="el-icon-edit"
                @click.stop="editJudgment(scope.row)"
              />
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <!-- 修改达成度 -->
    <el-dialog
      center
      title="修改达成度"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="50%"
    >
      <el-form
        :rules="rules"
        ref="JudgmentFormref"
        :model="Judgment"
        class="Judgment"
      >
        <el-form-item label="试卷ID">
          <el-input
            v-model="Judgment.tt_id"
            disabled
          />
        </el-form-item>
        <el-form-item
          label="试卷名称"
          disabled
        >
          <el-input v-model="Judgment.tt_name" />
        </el-form-item>
        <el-form-item
          label="评判值"
          prop="tt_reach"
        >
          <el-input
            type="number"
            v-model="Judgment.tt_reach"
          />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submit('JudgmentFormref')">提交</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 创建试卷/修改试卷 -->
    <el-dialog
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      width="50%"
    >
      <template slot="title">
        {{ addDialog }}
      </template>
      <el-form
        :rules="rules"
        ref="addtestFormref"
        :model="addtestForm"
        class="Judgment"
      >
        <el-form-item
          prop="tt_name"
          label="试卷名称"
        >
          <el-input v-model="addtestForm.tt_name" />
        </el-form-item>
        <el-form-item
          label="评判值"
          prop="tt_reach"
        >
          <el-input
            type="number"
            min="0"
            v-model="addtestForm.tt_reach"
          />
        </el-form-item>
        <el-form-item>
          <el-button
            class="addeqbtn"
            type="success"
            @click="addeq"
          >
            添加试题
          </el-button>
        </el-form-item>
        <el-form-item>
          <div
            v-for="item in addtestForm.eqList"
            :key="item.teq_id"
          >
            <div class="tabletitle">
              {{ item.teq_name }}
              <div class="eqbtn">
                <el-button
                  type="warning"
                  @click="altereq(item)"
                >
                  编辑试题
                </el-button>
                <el-button
                  type="danger"
                  @click="deleq(item)"
                >
                  删除试题
                </el-button>
              </div>
            </div>
            <div
              class="tableitem"
              v-for="item1 in item.knowledgePointsList"
              :key="item1.tkp_id"
            >
              <el-checkbox v-model="item1.check">
                {{ item1.tkp_title }}
              </el-checkbox>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submittset('addtestFormref')">提交</el-button>
        <el-button @click="handleClose1">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 添加试题 -->
    <el-dialog
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      width="50%"
    >
      <template slot="title">
        {{ addeqtitle }}
      </template>
      <el-form
        :rules="rules"
        ref="addeqFormref"
        :model="addeqForm"
      >
        <el-form-item
          prop="teq_name"
          label="试题名称"
        >
          <el-input v-model="addeqForm.teq_name" />
        </el-form-item>
        <el-form-item label="描述">
          <el-input
            type="textarea"
            v-model="addeqForm.teq_describe"
          />
        </el-form-item>
        <el-form-item>
          <div
            class="tableitem"
            v-for="item in addeqForm.knowledgePointsList"
            :key="item.tkp_id"
          >
            <el-checkbox v-model="item.check">
              {{ item.tkp_title }}
            </el-checkbox>
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submieq('addeqFormref')">提交</el-button>
        <el-button @click="handleClose2">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      u_id: { u_id: JSON.parse(window.sessionStorage.user).id },
      role: JSON.parse(window.sessionStorage.user).role.role_Id,
      addtab: '创建试卷',
      addDialog: '创建试卷',
      addeqtitle: '添加试题',
      addtestForm: {},
      addeqForm: {},
      testitem: {},
      isalter: false,
      iseqalter: false,
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible2: false,
      courselist: [],
      JudgmentTestlist: [],
      Judgment: {},
      activename: 'status',
      activeName1: 'first',
      testList: [],
      rules: {
        tt_reach: [{ required: true, message: '请输入达成度', trigger: 'blur' }],
        tt_name: [{ required: true, message: '请输入试卷名称', trigger: 'blur' }],
        teq_name: [{ required: true, message: '请输入试题名称', trigger: 'blur' }]
      },
      statusTabledata: [
        { name: '毕业要求', type: '必须', status: '完成', tips: '	毕业要求及指标点分解' },
        { name: '对应关系', type: '必须', status: '完成', tips: '	毕业要求与通用标准的对应关系' }
      ],
      agrList: [
        {
          name: '工程知识',
          desc: '能够将数学、自然科学、工程基础和专业知识用于解决复杂工程问题。',
          table: [{ name: '毕业要求1', desc: '能够将数学、自然科学、工程基础和计算机专业知识用于解决复杂工程问题。' }]
        },
        {
          name: '问题分析',
          desc:
            '能够应用数学、自然科学和工程科学的基本原理，识别、表达、并通过文献研究分析复杂工程问题，以获得有效结论。',
          table: [
            {
              name: '毕业要求1',
              desc: '能够将数学、自然科学、工程基础和计算机专业知识用于解决复杂工程问题。'
            },
            {
              name: '毕业要求2',
              desc:
                '能够应用数学、自然科学和工程科学的基本原理，识别、表达、并通过文献研究分析复杂工程问题，以获得有效结论。'
            }
          ]
        },
        {
          name: '设计/开发解决方案',
          desc:
            '能够设计针对复杂工程问题的解决方案，设计满足特定需求的系统、单元（部件）或工艺流程，并能够在设计环节中体现创新意识，考虑社会、健康、安全、法律、文化以及环境等因素。',
          table: [
            {
              name: '毕业要求3',
              desc:
                '能够设计针对复杂工程问题的解决方案，设计满足特定需求的系统、模块（组件）或算法流程，并能够在设计环节中体现创新意识，考虑社会、健康、安全、法律、文化以及环境等因素，运用所学理论和技术手段进行计算机系统和应用软件的分析、设计和开发。'
            }
          ]
        },
        {
          name: '研究',
          desc:
            ' 能够基于科学原理并采用科学方法对复杂工程问题进行研究，包括设计实验、分析与解释数据、并通过信息综合得到合理有效的结论。',
          table: [
            {
              name: '毕业要求4',
              desc:
                '能够基于科学原理并采用科学方法对复杂工程问题进行研究，包括设计计算机软硬件实验、分析与解释数据、并通过信息综合得到合理有效的结论。'
            }
          ]
        },
        {
          name: '使用现代工具',
          desc: '能够将数学、自然科学、工程基础和专业知识用于解决复杂工程问题。',
          table: [
            {
              name: '毕业要求5',
              desc:
                '	能够针对复杂工程问题，开发、选择与使用恰当的技术、资源、现代工程工具和信息技术工具，包括对复杂工程问题的预测与模拟，并能够理解其局限性。'
            }
          ]
        },
        {
          name: '工程与社会',
          desc:
            '能够基于工程相关背景知识进行合理分析，评价专业工程实践和复杂工程问题解决方案对社会、健康、安全、法律以及文化的影响，并理解应承担的责任。',
          table: [
            {
              name: '毕业要求6',
              desc:
                '能够基于工程相关背景知识进行合理分析，评价计算机类专业工程实践和复杂工程问题解决方案对社会、健康、安全、法律以及文化的影响，并理解应承担的责任。'
            }
          ]
        },
        {
          name: '环境与可持续发展',
          desc: '能够理解和评价针对复杂工程问题的专业工程实践对环境、社会可持续发展的影响。',
          table: [
            {
              name: '毕业要求7',
              desc: '能够理解和评价针对复杂工程问题的计算机工程实践对环境、社会可持续发展的影响。'
            }
          ]
        },
        {
          name: '职业规划',
          desc: '具有人文社会科学素养、社会责任感，能够在工程实践中理解并遵守工程职业道德和规范，履行责任。',
          table: [
            {
              name: '毕业要求8',
              desc: '具有人文社会科学素养、社会责任感，能够在工程实践中理解并遵守工程职业道德和规范，履行责任。'
            }
          ]
        },
        {
          name: '个人和团队',
          desc: '能够在多学科背景下的团队中承担个体、团队成员以及负责人的角色。',
          table: [
            {
              name: '毕业要求9',
              desc: '能够在多学科背景下的团队中承担个体、团队成员以及负责人的角色。'
            }
          ]
        },
        {
          name: '沟通',
          desc:
            '能够就复杂工程问题与业界同行及社会公众进行有效沟通和交流，包括撰写报告和设计文稿、陈述发言、清晰表达或回应指令。并具备一定的国际视野，能够在跨文化背景下进行沟通和交流。',
          table: [
            {
              name: '毕业要求10',
              desc:
                '能够就复杂工程问题与业界同行及社会公众进行有效沟通和交流，包括撰写报告和设计文稿、陈述发言、清晰表达或回应指令。并具备一定的国际视野，能够在跨文化背景下进行沟通和交流。'
            }
          ]
        },
        {
          name: '项目管理',
          desc: '理解并掌握工程管理原理与经济决策方法，并能在多学科环境中应用。',
          table: [
            {
              name: '毕业要求11',
              desc: '理解并掌握工程管理原理与经济决策方法，并能在多学科环境中应用。'
            }
          ]
        },
        {
          name: '终身学习',
          desc: '具有自主学习和终身学习的意识，有不断学习和适应发展的能力。',
          table: [
            {
              name: '毕业要求12',
              desc: '	具有自主学习和终身学习的意识，有不断学习和适应发展的能力。'
            }
          ]
        }
      ],
      teacheroptions: [],
      // 专业毕业要求数据
      specialityList: []
    }
  },
  created () {
    this.getmajoGraduation()
    this.getTestlist()
    this.getTestlist1()
    this.getteacherlist()
    this.getCourselist()
  },
  methods: {
    handleClick (tab, e) {
      this.$emit('getpath2', tab.label || this.addtab)
    },
    // 获取老师列表
    async getteacherlist () {
      const { data: res } = await this.$http.get('admin/showTeacher')
      res.obj.some(item => {
        const obj = { label: item.u_name, value: item.u_id }
        this.teacheroptions.push(obj)
      })
    },
    // 获取专业毕业要求信息
    async getmajoGraduation () {
      const { data: res } = await this.$http.get('mgr/showAll')
      this.specialityList = res.obj
    },
    // 获取试卷列表
    async getTestlist () {
      const { data: res } = await this.$http.get('test/showAll')
      this.testList = res.obj
      console.log(this.testList)
    },
    // 获取成绩管理列表
    async getCourselist () {
      const { data: res } = await this.$http.get('test/showControlTest')
      this.courselist = res.obj
      console.log(this.courselist)
    },
    // 获取达成度分析中的试卷
    async getTestlist1 () {
      const { data: res } = await this.$http.get('test/showAllTest')
      this.JudgmentTestlist = res.obj
    },
    // 添加试卷
    async addtest (item) {
      this.dialogVisible1 = true
      const { data: res } = await this.$htpp.post()
      const tc_id = item.course.tc_id
      this.addtestForm.tc_id = tc_id
    },
    // 编辑试卷
    async edittest (row) {
      this.dialogVisible1 = true
      this.isalter = true
      this.addDialog = '编辑试卷'
      this.addtestForm = row
    },
    // 删除试卷
    async deltest (row) {
      const tt_id = row.tt_id
      // 弹窗询问是否删除
      const confirmres = await this.$confirm('是否确认删除该试卷?', '确认删除', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.post('test/del', { tt_id })
      this.getTestlist()
    },
    // 添加试题
    async addeq () {
      this.dialogVisible2 = true
      const tt_id = this.addtestForm.tt_id
      const { data: res } = await this.$http.post('test/model', { tt_id })
      this.addeqForm = res.obj.eqList[0]
    },
    // 修改试题
    altereq (row) {
      this.addeqtitle = '修改试题'
      this.dialogVisible2 = true
      this.iseqalter = true
      this.addeqForm = row
    },
    // 删除试题
    async deleq (row) {
      // 弹窗询问是否删除
      const confirmres = await this.$confirm('是否确认删除该试卷?', '确认删除', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('已取消删除')
      }
      this.addtestForm.eqList.some((item, i) => {
        if (item.teq_id === row.teq_id) {
          this.addtestForm.eqList.splice(i, 1)
          return true
        }
      })
    },
    // 修改达成度
    editJudgment (row) {
      this.dialogVisible = true
      this.Judgment = row
      console.log(row)
    },
    // 提交修改/添加试卷
    submittset (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (this.isalter) {
          const addtestJson = JSON.stringify(this.addtestForm)
          console.log(addtestJson)
          const { data: res } = await this.$http.post('test/update', addtestJson)
        } else {
          this.addtestForm.u_id = JSON.parse(window.sessionStorage.user).id
          const { data: res } = await this.$http.post('test/add', this.addtestForm)
        }
        this.getTestlist()
        this.handleClose1()
      })
    },
    // 提交添加/修改试题
    submieq (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (!this.iseqalter) {
          console.log(this.addtestForm)
          this.addtestForm.eqList.push(this.addeqForm)
        }
        this.handleClose2()
      })
    },
    // 提交修改达成度
    submit (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        const { tt_id, tt_reach } = this.Judgment
        const data = { tt_id, tt_reach }
        const { data: res } = await this.$http.post('test/changeReach', data)
        this.dialogVisible = false
        this.getTestlist1()
      })
    },

    // Excel表格上传成功后
    success () {
      this.getCourselist()
    },
    handleChange () {
      setTimeout(() => {
        const right = document.getElementsByClassName('el-collapse-item__arrow el-icon-arrow-right')
        right.forEach(element => {
          if (element.className === 'el-collapse-item__arrow el-icon-arrow-right is-active') {
            element.previousSibling.previousSibling.className = 'iconfont icon-jianhao'
          } else {
            element.previousSibling.previousSibling.className = 'iconfont icon-jiahao'
          }
        })
      }, 50)
    },
    handleClose () {
      this.dialogVisible = false
    },
    handleClose1 () {
      this.isalter = false
      this.addtestForm = {}
      this.addDialog = '创建试卷'
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.addeqForm = {}
      this.dialogVisible2 = false
      this.iseqalter = false
      this.addeqtitle = '添加试题'
    }
  }
}
</script>
