.el-collapse,
.el-collapse-item__header {
  border-bottom: none;
}
.el-collapse-item {
  margin-bottom: 10px;
}
a {
  text-decoration: none;
}
.iconfont {
  color: #6091ba;
}
.addtestbtn {
  text-align: right;
  margin-bottom: 10px;
}
.upload-demo {
  text-align: right;
  margin-bottom: 10px;
  height: 28px;
  overflow: hidden;
}
.tableitem,
.tabletitle {
  border: 1px solid #e1e1e1;
  padding: 10px;
}
.tabletitle {
  background-color: #e1e1e1;
}
.eqbtn {
  float: right;
}
.el-form-item {
  overflow: hidden;
}
.addeqbtn {
  float: right;
}
</style>
